import React, { Component } from "react";
import axios from "../../../server/node_modules/axios";
import SearchArea from "./mySearchArea";
import MovieList from "./movie_list_Results";
import "./home.css";
import { Card, Row, Col, Container } from "react-bootstrap";
import { Redirect } from 'react-router-dom';
import ShowPage from "./showPage";


const baseImgUrl = "https://image.tmdb.org/t/p/w500";
let myKey = "d759a614b16c0c1c0295b4313e94aeec";

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            moviesTop: [],
            movies: [],
            isLoaded: false,
            moviesPopular: [],
            tvShowsTop: [],
            searchTerm: "",
            tvShowsPopular: [],
            totalResuolts: 0,
            currentPage: 1,
            flag : false
        };
        console.log("constructor");
    }
    // ______________________________________________
    async componentDidMount() {
        const response = await axios.get("http://localhost:8080/home");
        const moviesData = response.data.movies;
        console.log(moviesData);
        console.log("componentDidMount");
        this.setState({ moviesTop: moviesData });

        const res = await axios.get("http://localhost:8080/home/tv");
        const tvshowData = res.data.tvshow;
        console.log(tvshowData);
        console.log("componentDidMount2");
        this.setState({ tvShowsPopular: tvshowData });
    }

    // __________________________________________

    handleSubmit = e => {
        e.preventDefault();
        fetch(
            `https://api.themoviedb.org/3/search/movie?api_key=${myKey}&query=${this.state.searchTerm}`
        )
            .then(data => data.json())
            .then(data => {
                console.log(data);
                this.setState({
                    movies: [...data.results],
                    totalResuolts: data.total_results
                });
                console.log("search is done");
            });
    };
    // _________________________________
    handleChange = e => {
        this.setState({ searchTerm: e.target.value });
    };

    clickHandler = ()=>{
        this.setState({flag:true});
        


    }

    render() {
        if(this.state.flag){
            return(
                <Redirect to="/ShowPage"/>
            )
        }
        const homeMovies = this.state.moviesTop.map((movie, index) => (
            <Col md="3">
                <Card className="movie-card">
                    <Card.Img onClick={this.clickHandler}
                        variant="top "
                        src={`${baseImgUrl}/${movie.poster_path}`}
                        alt={"a"} />


                    <Card.Body>
                        <Card.Title className="card-title" key={index}>
                            {movie.title}
                        </Card.Title>
                        <Card.Text className="card-text">{movie.overview}</Card.Text>
                        <Card.Text className="card-text">{movie.vote_average}</Card.Text>
                    </Card.Body>
                </Card>
            </Col>
        ));

        const homeTvshow = this.state.tvShowsPopular.map((movie, index) => (
            <Col md="3">
                <Card className="movie-card">
                    <Card.Img
                        variant="top "
                        src={`${baseImgUrl}/${movie.poster_path}`}
                        alt={"a"}
                    />
                    <Card.Body>
                        <Card.Title className="card-title" key={index}>
                            {movie.name}
                        </Card.Title>
                        <Card.Text className="card-text">{movie.overview}</Card.Text>
                        <Card.Text className="card-text">{movie.vote_average}</Card.Text>
                    </Card.Body>
                </Card>
            </Col>
        ));
        // const numberPages = Math.floor(this.state.totalResuolts / 20);
        return (
            <div className="Home ">
                <h1>Welcome To Foxx Movie</h1>
                <Container>
                    <SearchArea
                        handleSubmit={this.handleSubmit}
                        handleChange={this.handleChange}
                    />
                </Container>

                <Container>
                    <Row>
                        <MovieList movies={this.state.movies} />
                    </Row>
                </Container>

                <div className="allMovies">
                    <Container>
                        <Row>
                            {!this.state.totalResuolts ? homeMovies : ""}
                            {!this.state.totalResuolts ? homeTvshow : ""}
                        </Row>
                    </Container>
                </div>
            </div>
        );
    }
}

export default Home;
